import { UserManager, WebStorageStateStore, Log } from "oidc-client";

const config = {
  authority: process.env.VUE_APP_IDS_URL,
  client_id: "portal.spa",
  redirect_uri: process.env.VUE_APP_SPA_PORTAL_URL + "/callback.html",
  silent_redirect_uri: process.env.VUE_APP_SPA_PORTAL_URL + "/silent.html",
  response_type: "id_token token",
  scope: "openid profile api.gateway",
  post_logout_redirect_uri: process.env.VUE_APP_SPA_PORTAL_URL,
  userStore: new WebStorageStateStore({ store: window.localStorage }),
  automaticSilentRenew: true
};

const mgr = new UserManager(config);

